<template>
	<div>
		<div class="Detailshead-head">
			<div class="Detailsheadbordercolor Detailshead-div1" @touchstart="Detailsheadbordercolor1">商品</div> 
			<div @touchstart="Detailsheadbordercolor2" class="Detailshead-div2">详情</div>
			<div @touchstart="Detailsheadbordercolor3" class="Detailshead-div3">评论</div>
		</div>
	</div>
</template>

<script lang="babel">
	export default {
		data() { //数据
			return {

			}
		},
		methods: { //js
			Detailsheadbordercolor1(){
				$(".Detailshead-head div").removeClass("Detailsheadbordercolor");
				$(".Detailshead-div1").addClass("Detailsheadbordercolor");
			},
			Detailsheadbordercolor2(){
				$(".Detailshead-head div").removeClass("Detailsheadbordercolor");
				$(".Detailshead-div2").addClass("Detailsheadbordercolor");
			},
			Detailsheadbordercolor3(){
				$(".Detailshead-head div").removeClass("Detailsheadbordercolor");
				$(".Detailshead-div3").addClass("Detailsheadbordercolor");
			}

		},
		components: { //注册模块
		},
		mounted() { //得到数据的ajax
			

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.Detailshead-head{
	padding: 0em 2em 0 2em;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 99;
    border-bottom: 1px solid #f1f1f1;
    display: flex;
		& div{
		font-size: 15px;
		position: relative;
		width: 1.6rem;
	    -webkit-box-flex: 1;
		text-align: center;
	    margin: 0 0.3em;
	    padding: 0.65em 0;
	    color: #605F5F;
	    line-height: 0.37rem;
		}
	}
	.Detailsheadbordercolor{
		border-bottom: 4px solid #ff0000;
	}
</style>


