<template>
  <div>
	  	<div class="swiper-container Detailsheadpd" >
		    <div class="swiper-wrapper">
		        <div class="swiper-slide" v-for="obj in listArr"><router-link :to="obj.toUrl"><img :src="obj.imgUrl"/></router-link></div>
		    </div>
		    <!-- 如果需要分页器 -->
		    <div class="swiper-pagination pag"></div>
			</div>
  </div>
</template>
<script lang="babel">
export default {
	props: {//从父得到数据
			listArr:Array
		},
	data(){//数据
		return{	
		}	
	},
	methods:{//js
		
	},
	components:{//注册模块
	},
	updated() {
		var mySwiper = new Swiper('.swiper-container',{
		pagination : '.swiper-pagination',
		autoplay: 3000,
		paginationType : 'fraction',
				//paginationType : 'progress',
				//paginationType : 'custom',
		})
		
	},
	mounted(){//得到数据的ajax
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
 .pag{
 	bottom: 0;
 	left: 50%;
 	    margin-left: -.4rem;
 }
 .Detailsheadpd{
 	background: #f7f7f7;
 }
 .Detail-div{
 	font-size: 1.1em;
  padding: 0.3em 0.5em;
  background: #fff;
  position: relative;
  color: #474747;
 }
</style>


