<template>
	<div class="headpluin-dabian">
		<div class="dabian">
			<div style="display: flex;" >
				<div class="headpluin-head"><i class="fa fa-list-ul" style="color: #d8d8d8;"></i></div>
				<div style="display: flex;margin-top: 0.16rem;" @touchstart="according">
					<div class="headpluin-head-div">
						<i class="fa fa-search"></i>
					</div>
					<div class="headpluin-head-div-2"></div>
				</div>
				<div class="headpluin-fenlei" @touchstart="headpluinfenlei">
					
				</div>
			</div>
			<div class="headplugin">
				<div @touchstart="headpluginjs1"><div>默认排序</div><i class="fa fa-angle-up headplugini1"></i></div>
				<div @touchstart="headpluginjs2" @touchend="headpluginjs2"><div>销量</div></div>
				<div @touchstart="headpluginjs3" @touchend="headpluginjs3"><div>价格</div><i class="fa fa-angle-up headplugini2"></i></div>
				<div  @touchstart="headpluginjs4"><div>分类</div><i class="fa fa-angle-left headplugini3"></i></div>
			</div>
			<div class="headpluin-hide">
				<div class="headplugin-div-1" @touchstart="Integrated">综合排序</div>
				<div class="headplugin-div-2 headplugin-color" @touchstart="Integrated2">新品排序</div>
			</div>
			<div class="headpluinnone">该分类没有更多了</div>
		</div>
		<searchfor></searchfor>
	</div>
</template>

<script lang="babel">
	import searchfor from "./searchfor";
	export default {

		data() { //数据
			return {
				bool:false,
				youth:[],
				bool2:false,
				bool3:false,
				bool4:false
			}
		},
		methods: { //js
			according() { //显示
				$(".position").css({ "display": "" });
				$(".app").css({"overflow":"hidden"});
				$(".dabian").css({"display": "none"});
			},
			headpluginjs1(){//隐藏显示
				if(this.bool==true){
					$(".headplugini1").removeClass("fa-angle-down");
					$(".headplugini1").addClass("fa-angle-up");
					$(".headpluin-hide").css({display:"none"});
					this.bool=false;
				}
				else{
					$(".headplugini1").removeClass("fa-angle-up");
					$(".headplugini1").addClass("fa-angle-down");
					$(".headpluin-hide").css({display:"block"});
					this.bool=true;
				}
			},
			headpluginjs2(){//销量排序				
				var _this=this;
				var obj=[];
				var divNode=$(".youthpleft");
				var youthdiv=$(".youthdiv");
				var p=-1;//举例一个顺序位置
				console.log(_this.youth,$(".youthdiv").length)
				if(_this.youth.length<$(".youthdiv").length){
					
				 window.location.reload();//刷新当前页面.
				}
				for(var i=0;i<_this.youth.length;i++){
					obj.push(_this.youth[i].Cross)//得到销量的数组Cross是json销量	
				}
				function sortNumber(a,b)
				{
				return b - a
				}
				for(var i=0;i<divNode.length;i++){
					var digital=divNode.eq(i).text().substring(0,divNode.eq(i).text().length-3);//得到每个节点的文本并且截取后三位得到文本中销量的数字
						for(var k=0;k<$(".youthdiv").length;k++){//循环大到小排序的函数sort(sortNumber)是大到小
							if(obj.sort(sortNumber)[k]==digital){//判断排序的值是否等于文本数字的值
								p++;					
								
								//console.log(divNode.eq(p).text(),$(".youthpleft").eq(k).text())
								if(divNode.eq(p).text()!=$(".youthpleft").eq(k).text()){//判断得到的eq文本是否和k中的值一样，一样则不调换，k是排序后的数字
								youthdiv.eq(p).insertBefore($(".youthdiv").eq(k))//把顺序位置放在排序位置的后面
								}
							}		
						}
					}
				},
			headpluginjs3(e){
				var _this=this;
				var obj=[];
				var divNode=$(".youthp2");
				var youthdiv=$(".youthdiv")
				var p=-1;//举例一个顺序位置
				if(_this.youth.length<$(".youthdiv").length){
					
				 window.location.reload();//刷新当前页面.
				}
				for(var i=0;i<_this.youth.length;i++){
					obj.push(_this.youth[i].digital.substr(1,divNode.eq(i).text().length));//得到销量的数组digital是json销量	
//					console.log(obj)
				}
				if(e.type == "touchstart"){
					if(this.bool3  ){
						$(".headplugini2").removeClass("fa-angle-down");
						$(".headplugini2").addClass("fa-angle-up");
						this.bool3=false;
					}
					else{
					$(".headplugini2").removeClass("fa-angle-up");
					$(".headplugini2").addClass("fa-angle-down");
					this.bool3=true;
					}
				}
				if(this.bool3==true){
					var sortNumber=function (a,b)
					{
					return b - a
					}
				}
				else{
					var sortNumber=function (a,b)
					{
					return a - b
					}
				}
				for(var i=0;i<divNode.length;i++){
					var digital=divNode.eq(i).text().substr(1,divNode.eq(i).text().length);//得到每个节点的文本并且截取后三位得到文本中销量的数字
						for(var k=0;k<$(".youthdivjs2").length;k++){//循环大到小排序的函数sort(sortNumber)是大到小
							if(obj.sort(sortNumber)[k]==digital){//判断排序的值是否等于文本数字的值
								p++;					
//								console.log(k,youthdiv.eq(p),k)
								//console.log(divNode.eq(p).text(),$(".youthpleft").eq(k).text())
								if(divNode.eq(p).text()!=$(".youthp2").eq(k).text()){//判断得到的eq文本是否和k中的值一样，一样则不调换，k是排序后的数字
								youthdiv.eq(p).insertBefore($(".youthdiv").eq(k))//把顺序位置放在排序位置的后面
								
								}
							}		
						}
					}
				
			},
			headpluginjs4(){
				if(this.bool2){
					$(".headpluinnone").css({display:"none"});
					$(".headplugini3").removeClass("fa-angle-down");
					$(".headplugini3").addClass("fa-angle-left");
					this.bool2=false;
				}
				else{
				$(".headpluinnone").css({display:"block"});
				$(".headplugini3").removeClass("fa-angle-left");
				$(".headplugini3").addClass("fa-angle-down");
				this.bool2=true;
				}
			},
			Integrated(){
				$(".headplugin-div-1").addClass("headplugin-color");
				$(".headplugin-div-2").removeClass("headplugin-color");
				$(".headpluin-hide").css({display:"none"});
				$(".headplugini1").removeClass("fa-angle-down");
				$(".headplugini1").addClass("fa-angle-up");
				this.bool=false;
			},
			Integrated2(){
				$(".headplugin-div-2").addClass("headplugin-color");
				$(".headplugin-div-1").removeClass("headplugin-color");
				$(".headpluin-hide").css({display:"none"});
				$(".headplugini1").removeClass("fa-angle-down");
				$(".headplugini1").addClass("fa-angle-up");
				this.bool=false;
			},
			headpluinfenlei(){
				if(this.bool4==true){
					$(".youthdivjs1").removeClass("youthdiv-modify");
					$(".youthdivjs2").removeClass("div-modify");
					$(".youthdivjs3").removeClass("youdivhidden-modify");
					$(".youthdivjs4").removeClass("youthpp-modify");
					this.bool4=false;
				}
				else{
					$(".youthdivjs1").addClass("youthdiv-modify");
					$(".youthdivjs2").addClass("div-modify");
					$(".youthdivjs3").addClass("youdivhidden-modify");
					$(".youthdivjs4").addClass("youthpp-modify");
					this.bool4=true;
				}
			}
		},
		components: { //注册模块
			searchfor
		},
		mounted() { //得到数据的ajax
			var _this = this;
			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/youth.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.youth = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.headplugin-color{
		color: #f00;
	}
	.headpluin-dabian{
		background: #fff;position: fixed;width: 100%;
		z-index: 2;
	}
	.headpluin-head{
		width: 11%;
		text-align: center;
		line-height: .69rem;
	}
	.headpluin-head-div{
		background: #f3f3f3;
		width:26px;
		color: #dbdbdb;
		border: 1px #dbdbdb solid; 
		border-right: none;
		border-radius: 0.1rem 0 0 0.1rem;
		padding: 0.05rem 0 0.08rem 0.08rem;
		height: 0.3rem;
		line-height: 0.3rem;
	}
	.headpluin-head-div-2{
		background: #f3f3f3;
		width: 4.4rem;
		border: 1px #dbdbdb solid; 
		border-radius:  0 0.1rem  0.1rem 0 ;
		border-left: none;
		height:  0.43rem;
	}
	.headpluin-fenlei{
		width: 0.3rem;
	    height: 0.3rem;
	    margin: .23rem 0 0 .30rem;
		background: url("../../assets/images/fenlei.jpg")no-repeat;
	}
	.headplugin{
		display: flex;
		padding-bottom: 0.3rem;
		& div{
			margin: 0.2rem 0.39rem 0px 0.51rem;
			font-size: 13px;
			line-height: 17px;
			display: flex;
			& div{
				margin: 0;
			}
		}
	}
	.headplugini1,.headplugini{
		padding-top: 0.03rem;
		padding-left: 0.06rem;
	}
	.headplugin-div-1,.headplugin-div-2{
		height:  0.61rem;
		font-size: 13px;
		line-height:  0.61rem;
		border-bottom: 1px solid #f1f1f1;
		padding-left: 0.2rem;
	}
	.headpluin-hide{
	padding-left: 0.14rem;display: none;
		}
	.headpluinnone{
		display: none;
		height: 0.98rem;
		line-height: 0.98rem;
		text-align: center;
		font-size: 14px;
	}
</style>


