<template>
  <div class="tail">
  	<router-link to="/" style="display: block;width: 16%;">
  		<div class="page">
  			<i class="page-i"></i>
  		</div>
  	</router-link>
  	<router-link to="/classification" style="display: block;width: 28%;">
  	<div class="classification">
  		<div class="bars"><i class="fa fa-bars "></i></div>
  		<span class="baspan">分类</span>
  	</div>
  	</router-link>
  	<div class="activity" @touchstart="activity">
  		<div class="bars-2"><i class="fa fa-bars "></i></div>
  		<span class="baspan-2">活动</span>
  		<div class="activity-pos">
  			<div class="activity-pos-div">
  				<router-link to="/Refreshing"><div>清爽夏季</div></router-link>
  				<router-link to="/"><div>肌肤密语</div></router-link>
  				<router-link to="/"><div>我的发现</div></router-link>
  			</div>
  			<div class="activity-pos-div-pos"></div>
  		</div>
  	</div>
  	<div class="my" @touchstart="my">
  		<div class="bars-3"><i class="fa fa-bars "></i></div>
  		<span class="baspan-3">我的</span>
  		<div class="activity-pos2">
  			<div class="activity-pos-div">
  				<router-link to="/"><div>订单</div></router-link>
  				<router-link to="/"><div>会员中心</div></router-link>
  				<router-link to="/"><div>购物车</div></router-link>
  			</div>
  			<div class="activity-pos-div-pos">
  			</div>
  		</div>
  	</div>
  </div>
</template>

<script lang="babel">

export default {
	data(){//数据
		return{
			bool:false,
			boolmy:false
		}	
	},
	methods:{//js
		activity(){//功能显示隐藏
			var _this=this;
			if(_this.bool==true){
				$(".activity-pos").fadeOut(300);
				_this.bool=false;
			}
			else{	
				_this.bool=true;
				$(".activity-pos").fadeIn(300);
			}
			if($(".activity-pos2").css("display")=="block"){
				$(".activity-pos2").fadeOut(300);
				$(".activity-pos").fadeIn(300);
			}
		},
		my(){//功能显示隐藏
			var _this=this;
			if(_this.boolmy==true){
				$(".activity-pos2").fadeOut(300);
				_this.boolmy=false;
			}
			else{	
				_this.boolmy=true;
				$(".activity-pos2").fadeIn(300);
			}
			if($(".activity-pos").css("display")=="block"){
				$(".activity-pos").fadeOut(300);
				$(".activity-pos2").fadeIn(300);
			}
		}
	},
	components:{//注册模块
	},
	mounted(){//得到数据的ajax
		
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
.tail{width: 100%;height: 41px;position:fixed; bottom: 0px;background: #f7f7f7;box-sizing: border-box;display: flex;border-top: 1px solid #ccc;left: 0px;}
.page{width: 100%;height: 100%;text-align: center;line-height: 41px;box-sizing: border-box;padding: 10px 0px 0px 23px;border-right: 1px solid #dddddd;}
.page-i{width: 20px;height: 20px;background: url(../../assets/images/page.jpg) no-repeat;display: block;}
.bars,.bars-2,.bars-3{width:14px ;height: 18px;line-height: 18px;text-align: center;}
.classification,.activity,.my{height: 100%;padding:10px 0px 0px 33px;box-sizing: border-box;& i{color: #929292;}display: flex;border-right:1px solid #ddd;}
.activity,.my{width: 28%;}
.baspan,.baspan-2,.baspan-3{width: 38px;height: 18px;display: block;line-height: 18px;color: #929292;margin-left: 3px;}
.activity-pos{width: 30%;position: absolute;top: -143px;left: 40%;background: #f7f7f7;border-radius: 10px;padding-left:  13px;display: none;}
.activity-pos-div{line-height: 42px;font-size: 16px;& a{display: block;border-bottom: 1px #e0dfe2 solid;} & a:nth-child(3){border: none;}}
.activity-pos-div-pos{position: absolute;width: 0px;height: 0px;border:13px #f7f7f7 solid;left: 66px;border-color: #f7f7f7 transparent transparent transparent;}
.activity-pos2{width: 30%;position: absolute;top: -143px;left: 66%;background: #f7f7f7;border-radius: 10px;padding-left:  13px;display: none;}
</style>


