<template>
	<div class="head">
		<div class="menu" @touchstart="appfun" @cilck="appfun">
			<div class="fa fa-bars "></div>
		</div>
		<div class="search">
			<div class="glass"><i class="fa fa-search"></i></div>
			<div class="input" @touchstart="according">搜索</div>
		</div>
		<div class="gender">
			<div class="neuter">
				<i class="fa fa-neuter"></i>
			</div>
			<div class="stroke">
				<i class="fa fa-mars-stroke"></i>
			</div>
		</div>
		<searchfor></searchfor>
		<div class="cloth" v-if="showbool" @touchstart="rightfun">
		</div>

	</div>
</template>

<script lang="babel">
	import searchfor from "./searchfor";
	export default {
		data() { //数据
			return {
				showbool: false				

			}
		},
		methods: { //js
			according() { //显示
				$(".position").css({ "display": "" });
				$(".app").css({"overflow":"hidden"});
			},
			appfun() { //整个app向右移动
				var app = $(".v-hidde");
				if(app.is(".slatefo")) {
					app.removeClass("slatefo");
					$(".height").css({ overflow: "hidden" })
				}
				app.addClass("slate");
				$(".app").css({"overflow":"hidden"});
				this.showbool = true;
			},
			rightfun() { //整个app向右移动
				var app = $(".v-hidde");
				if(app.is(".slate")) {
					app.removeClass("slate");
					$(".height").removeAttr("style");
				}
				app.addClass("slatefo");
				$(".app").removeAttr("style");
				this.showbool = false;
			}

		},
		components: { //注册模块
			searchfor
		},
		mounted() { //得到数据的ajax
			

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.cloth {
		width: 100%;
		height: 100%;
		position: absolute;
		z-index: 99;
		left: 0px;
		top: 0px;
	}
	
	.head {
		width: 100%;
		height: 44px;
		background: #f3f3f3;
		display: flex;
	}
	
	.menu {
		width: 10%;
		height: 44px;
		text-align: center;
		line-height: 44px;
	}
	
	.input {
		width: 70%;
		height: 31px;
		border-radius: 0px 15px 15px 0px;
		border: none;
		background: #fff;
		color: #D8D8D8;
		font-size: 1em;
		line-height: 31px;
	}
	
	.search {
		width: 70%;
		height: 31px;
		margin-top: 7px;
		display: flex;
	}
	
	.glass {
		width: 20%;
		height: 31px;
		text-align: center;
		line-height: 31px;
		background: #fff;
		border-radius: 15px 0px 0px 15px;
	}
	
	.fa-search {
		color: #e3e3e3;
	}
	
	.gender {
		margin-top: 7px;
		display: flex;
	}
	
	.neuter,
	.stroke {
		width: 28px;
		height: 28px;
		text-align: center;
		line-height: 28px;
		color: #fff;
	}
	
	.neuter {
		background: #ccc;
	}
	
	.stroke {
		background: #d63838;
		margin-left: 3px;
	}
	
	
	
	.angle {
		width: 21px;
		height: 29px;
		text-align: center;
		line-height: 29px;
	}
</style>


