<template>
	<div>
		<div class="cationfixed" @touchstart="cationfixed">
			<div class="cationfixeddiv">
				<div class="cationfixedfen">
					<router-link to="/"><span>首页</span></router-link>
					<router-link to="/classification"><span>分类</span></router-link>
					<router-link to="/"><span>服务</span></router-link>
					<router-link to="/"><span style="border: none;">中心</span></router-link>
				</div>
				<div class="cationfixedhaed"></div>
			</div>
			<p style="margin-left: 0.13rem;"></p>
			<p></p>
			<p></p>
		</div>
		<div class="cationfixedbu" @touchstart="cationfixednone"></div>
	</div>
</template>

<script lang="babel">
	export default {

		data() { //数据
			return {

			}
		},
		methods: { //js
			cationfixed() {
				$(".cationfixedbu").css({ display: "block" })
				$(".cationfixeddiv").fadeIn(200);
			},
			cationfixednone() {
				$(".cationfixedbu").css({ display: "none" })
				$(".cationfixeddiv").fadeOut(200);
			}
		},
		components: { //注册模块
		},
		mounted() { //得到数据的ajax
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.cationfixed {
		height: 2.5em;
		width: 2.5em;
		border-radius: 0.5em;
		line-height: 1.3em;
		display: flex;
		position: fixed;
		bottom: 4em;
		right: 0.4em;
		background: rgba(133, 133, 133, 0.65);
		text-align: center;
		z-index: 8;
		& p{
			margin:0.31rem 0px 0px 0.1rem;
			width: .08rem;
			height: .08rem;
			border-radius: 1px;
			background: #fff;
			
		}
	}
	
	.cationfixedbu {
		position: fixed;
		z-index: 4;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: rgba(0, 0, 0, 0.3);
		display: none;
	}
	
	.cationfixeddiv {
		width: 86px;
		position: absolute;
		top: -144px;
		left: -44px;
		display: none;
		& div {
			background-color: #fff;
		}
	}
	
	.cationfixedfen {
		border-bottom: 1px solid #f1f1f1;
		position: relative;
		z-index: 9;
		border-radius: 5px;
		& a span {
			display: block;
			line-height: 29px;
			border-bottom: 1px solid #f1f1f1;
			font-size: 15px;
		}
	}
	
	.cationfixedhaed {
		width: 0px;
		height: 0px;
		border: 13px solid;
		position: absolute;
		z-index: 9;
		border-color: #fff transparent transparent transparent;
		background-color: transparent !important;
		right: 10px;
		bottom: -24px;
	}
</style>


