<template>
  <div style="padding-top: 0.7rem;">
  	<Detailshead></Detailshead>
  	<list :listArr="Details1"></list>
  	<div class="Details-div clearfix">
  		<div class="Details-div-size">
  			<span>新品</span>三福2017夏装新品男褶皱棉质衬衫 休闲印花短袖衬衣男372207
  		</div>
  		<div style="margin: 0.2em 0;">
  			<div>
  				<span style="font-size: 18px;color: #ff0000;">￥119.00</span>
  			</div>
  		</div>
  		<span class="Detailsleft">降价提醒</span><span class="Detailsright">销量12件</span>
  		<div style="padding-top: 0.8em;margin-top: 0.5em;display: flex;">
  			<div>促销</div>
  			<div>
  				<div>
  					<span>包邮</span>
  					<span>全场满99包邮</span>
  				</div>
  			</div>
  			<div>
  				<i class="fa fa-angle-up"></i>
  			</div>
  		</div>
  	</div>
  	<Detaillist></Detaillist>
  </div>
</template>
<script lang="babel">
import Detailshead from "./common/Detailshead";
import Detaillist from "./common/Detaillist";
import list from "./common/Switchthebar";
export default {
	data(){//数据
		return{
			Details1:[]
		}	
	},
	methods:{//js
		
	},
	components:{//注册模块
	Detailshead,
	Detaillist,
	list
	},
	updated() {
		
	},
	mounted(){//得到数据的ajax
		var _this = this;

			_this.$http({
				method: 'get', //ajax类型 方法
				url: "./static/json/Details1.json", //请求地址(接口)
				params: {} //data发送后台 参数
			}).then(function(data) {
				//然后data后台响应的数据;success进入 //赋值给sliderArr数组,sliderArr滑动数组
				_this.Details1 = data.data; //同步值；或_this.xxxArr=data.body;
			}, function(error) {
				//错误的时候
				alert("数据请求失败请稍后再试");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
.Details-div{
  font-size: 1.1em;
  padding: 0.3em 0.5em;
  background: #fff;
  position: relative;
  color: #474747;
}
.Details-div-size{
	font-size: 16px;
	position: relative;
	& span{
		font-size: 0.8em;
    border-radius: 0.6em;
    background: #ff0000;
    color: #fff;
    padding: 0 0.5em;
    margin-right: 0.5em;
	}
}
.Detailsleft{
	  color: rgb(175, 174, 174);
    border-radius: 0.3em;
    padding: 0px 0.5em;
    float: left;
    margin-top: 0em;
    margin-right: 0.2em;
    font-size: 0.75em;
    border: 1px solid rgb(175, 174, 174);
}
.Detailsright{
	    color: red;
    background: #fff;
    border: 1px solid red;
    border-radius: 0.3em;
    padding: 0.08em 0.5em;
    float: right;
    margin-top: 0em;
    margin-right: 0.2em;
    font-size: 0.75em;
}
</style>


